import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { InfoPage } from '../info/info';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/finally';

/**
 * Generated class for the OralPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-oral',
  templateUrl: 'oral.html',
})
export class OralPage {

  lista:any;
  show=true;
  items:any;
  isToggled:any;


  
  private carregando: boolean = false;

  constructor(public navCtrl: NavController, public http: HttpClient) {
    this.getData();
  }

  /*public notify() {
    console.log("Toggled: "+ this.isToggled); 
  }*/

  getData(){
    this.carregando = true;
    let url = '';
    if(this.isToggled==1){ url = 'http://www.metallum.com.br/23cbecimat/app-lista/json.php?t=oral&t2=palestra';}
    else{ url = 'assets/lista.json';}
    console.log(url);
    let data:Observable<any> = this.http.get(url);
    data.finally(() => {this.carregando = false; this.initializeItems();})
      .subscribe(result=>{
      this.lista = result;
      console.log( this.lista);
    })
    
  }


  initializeItems() {
    this.items=this.lista;
  }

 
 getDias() {
    this.show=false;
    this.initializeItems();
    const val = event.srcElement.id;
    if (val && val.trim() != '' && val!='all') {
      this.items = this.items.filter((item) => {
        return (
          item.apres_data.toLowerCase().indexOf(val.toLowerCase()) > -1 && 
          (item.tipo.toLowerCase().indexOf('oral'.toLowerCase()) > -1 ||  
          item.tipo.toLowerCase().indexOf('palestra'.toLowerCase()) > -1)
        );
      })
    }
    else{
      this.items = this.items.filter((item) => {
        return (
          item.tipo.toLowerCase().indexOf('oral'.toLowerCase()) > -1 ||  
          item.tipo.toLowerCase().indexOf('palestra'.toLowerCase()) > -1
        );
      })
    }
    
  }

  getItems(ev: any) {
    this.initializeItems();
    const val = ev.target.value;
    //console.log(this.items);
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (
          (
            item.referencia && item.titulo && 
            item.apres_sessao && item.apres_data 
            && item.nome && item.tipo
          ) &&
          (item.referencia.toLowerCase().indexOf(val.toLowerCase()) > -1 ||
          item.titulo.toLowerCase().indexOf(val.toLowerCase()) > -1 ||
          item.apres_sessao.toLowerCase().indexOf(val.toLowerCase()) > -1 ||
          item.apres_data.toLowerCase().indexOf(val.toLowerCase()) > -1 ||
          item.nome.toLowerCase().indexOf(val.toLowerCase()) > -1 ||
          item.tipo.toLowerCase().indexOf(val.toLowerCase()) > -1)
        );
        
      })
      
    }
    
  }


  viewInfo(item:any){
    this.navCtrl.push(InfoPage, {programa:item});
  }

  ionViewDidLoad() {
    //console.log('ionViewDidLoad OralPage');
  }

}



